import React from "react";
import { FormRadio } from "shards-react";

export default function QuestionOption({
  selectOption,
  name,
  value,
  next,
  description,
  text = null,
}) {
  if (text === null) {
    text = value;
  }
  return (
    <div className="my-4">
      <FormRadio
        name={name}
        value={value}
        onClick={() => selectOption(name, value, next)}
      >
        <strong>{text}</strong>
        <p>{description}</p>
      </FormRadio>
    </div>
  );
}
