import React from "react";
import { Button } from "shards-react";
import QuestionOption from "./QuestionOption";

export default function Question({
  children,
  backQuestion,
  switchQuestion,
  selectOption,
  question,
  nextQuestion,
  prevQuestion,
  name,
  options = [],
  position = null,
}) {
  return (
    <>
      <h4 className="my-5">{question}</h4>
      {options.map((option) => (
        <QuestionOption
          key={option.value}
          {...option}
          name={name}
          selectOption={selectOption}
        />
      ))}
      <hr />
      {position !== "first" && (
        <Button
          type="button"
          theme="light"
          onClick={() => backQuestion(prevQuestion)}
        >
          Back
        </Button>
      )}
      {position === "first" && (
        <Button
          type="button"
          block
          onClick={switchQuestion}
          disabled={nextQuestion === name}
        >
          Get Started
        </Button>
      )}
      {position !== "first" && (
        <Button
          type="button"
          className="float-right"
          onClick={switchQuestion}
          disabled={nextQuestion === name}
        >
          Next
        </Button>
      )}
    </>
  );
}
