import React, { useState } from "react";
import { Form, Alert, FormGroup, FormInput, Button } from "shards-react";
import Question from "./Question";
import questions from "../data/questions";

export default function FormSurvey() {
  const [currentQuestion, setCurrentQuestion] = useState("question-0");
  const [nextQuestion, setNextQuestion] = useState("question-0");
  const [data, setData] = useState(new Map());

  function switchQuestion() {
    setCurrentQuestion(nextQuestion);
  }

  function backQuestion(prevQuestion) {
    setCurrentQuestion(prevQuestion);
    setNextQuestion(prevQuestion);
  }

  function selectOption(name, value, nextQuestion) {
    setNextQuestion(nextQuestion);
    data.set(name, value);
    setData(data);
  }

  function submitForm(e) {
    e.preventDefault();
    const form = new FormData(e.target);
    const formData = Object.fromEntries(form);
    const questionData = Object.fromEntries(data);
    const postData = { ...formData, ...questionData };
    console.log(postData);
    fetch("https://api.github.com/gists", {
      method: "post",
      body: JSON.stringify(postData),
    }).then(function (response) {
      return response.json();
    });
  }

  return (
    <Form onSubmit={(e) => submitForm(e)}>
      {typeof currentQuestion === "undefined" && (
        <>
          <h4>Success! Let's connect you with talent.</h4>
          <FormGroup>
            <FormInput type="email" placeholder="Email" name="email" required />
          </FormGroup>
          <FormGroup>
            <FormInput
              placeholder="Company name"
              name="company_name"
              required
            />
          </FormGroup>
          <FormGroup>
            <FormInput
              placeholder="Contact name"
              name="contact_name"
              required
            />
          </FormGroup>
          <hr />
          <Button block>Connect Me With Talent</Button>
        </>
      )}
      {/* {currentQuestion === "question-0" && (
        <Alert theme="light">
          Thanks for your interest in hiring through Toptal! Before we get
          started, we’d like to ask a few questions to better understand your
          business needs.
        </Alert>
      )} */}
      {questions.map(
        (question) =>
          currentQuestion === question.name && (
            <Question
              key={question.name}
              {...question}
              selectOption={selectOption}
              switchQuestion={switchQuestion}
              backQuestion={backQuestion}
              nextQuestion={nextQuestion}
              prevQuestion={question.prev}
            />
          )
      )}
    </Form>
  );
}
